import React from 'react';
import ReactDOM from 'react-dom';

const data = [
    { category: "Sporting Goods", price: "$49.99", stocked: true, name: "Football" },
    { category: "Sporting Goods", price: "$9.99", stocked: true, name: "Baseball" },
    { category: "Sporting Goods", price: "$29.99", stocked: false, name: "Basketball" },
    { category: "Electronics", price: "$99.99", stocked: true, name: "iPod Touch" },
    { category: "Electronics", price: "$399.99", stocked: false, name: "iPhone 5" },
    { category: "Electronics", price: "$199.99", stocked: true, name: "Nexus 7" }
];

class ProductRow extends React.Component {
    render() {
        var nameElement = <td>{this.props.name}</td>;
        if(!this.props.isStocked)
        {
            nameElement = <td style={{color:'red'}}>{this.props.name}</td>;
        }
        return (
            <tr>
                {nameElement}
                <td>{this.props.price}</td>
            </tr>
        )
    }
}

class ProductCategory extends React.Component {
    render() {
        return (
            <tr><th>{this.props.category}</th></tr>
        )
    }
}

class TitleComponent extends React.Component {
    render() {
        return (
            <table>
                <tr>
                    <th>Name</th>
                    <th>Price</th>
                </tr>
                {this.props.renderProducts}
            </table>
        )
    }
}

class SearchComponent extends React.Component {
    render() {
        return (
            <div>
                <input type='text' placeholder='Search...' onChange={this.props.handleTextChange} value={this.props.searchText}></input>
                <div>
                    <input id='stokeCheck' type='checkbox' onChange={this.props.handleStockChange} value={this.props.stocked}></input>
                    <label htmlFor='stokeCheck'>Only show products in stock</label>
                </div>
            </div>
        )
    }
}

class ProductComponent extends React.Component {
    getProductDetails() {
        const products = this.props.products;
        const element = products.map((item, index) =>
            <ProductRow key={item.name + index} name={item.name} price={item.price} isStocked={item.stocked}></ProductRow>
        )
        return element;
    }
    render() {
        if (this.props.products.length <= 0)
            return null;
        return (
            <tr>
                <ProductCategory category={this.props.category}></ProductCategory>
                {this.getProductDetails()}
            </tr>
        )
    }
}

class ProductMaintanence extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            searchText: '',
            stocked: false,
            productList: []
        }

        this.handleTextChange = this.handleTextChange.bind(this);
        this.handleStockChange = this.handleStockChange.bind(this);
    }

    handleStockChange(event) {
        this.setState({
            stocked: event.target.checked
        }, () => {
            this.setProductList();
        })
    }

    componentDidMount() {
        this.setProductList()
    }

    setProductList() {
        const diffCategories = [...new Set(data.map(item => item.category))]
        const _PL = []
        diffCategories.forEach((item) => {
            let _obj = {}
            _obj['category'] = item;
            _obj['products'] = data.filter(value => this.getProductonSearch(item, value))
            _PL.push(_obj)
        })
        this.setState({
            productList: _PL
        })
    }

    getProductonSearch(item, value) {
        return (value.category === item && (this.state.searchText === "" || value.name.indexOf(this.state.searchText) > -1) &&
            (this.state.stocked === false || value.stocked === this.state.stocked))
    }

    handleTextChange(event) {
        this.setState({
            searchText: event.target.value
        }, () => {
            this.setProductList();
        });
    }

    getProducts(category) {
        return data.filter(item => { return (item.category === category) })
    }

    renderProducts() {
        const element = this.state.productList.map((item, index) =>
            <ProductComponent key={item.category + index} category={item.category} products={item.products}></ProductComponent>
        )
        return element;
    }

    render() {
        return (
            <div>
                <SearchComponent handleStockChange={this.handleStockChange}
                    handleTextChange={this.handleTextChange}
                    searchText={this.state.searchText}
                    stocked={this.state.stocked}></SearchComponent>
                <TitleComponent renderProducts={this.renderProducts()}></TitleComponent>
            </div>
        )
    }
}

ReactDOM.render(
    <ProductMaintanence></ProductMaintanence>,
    document.getElementById('root')
)